import React from 'react'
import {Button } from 'react-bootstrap'


import './recipe.model.css';



const Recipe =({title,calories,image,cautions,ingredients})=>{
    return(
        <div className="recipes">
           
            <img className="image" src={image} alt="" />
            <h1 style={{letterSpacing:1.5}}><b>{title}</b></h1>
            <Button style={{fontSize:18,fontWeight:800,marginBottom:10}} variant="primary">
         Details
        </Button>



      
              
                <ol style={{margin:10}}>
                {ingredients.map(ingredient=>(
                <li style={{fontSize:16}}><b>{ingredient.text}</b></li>
                ))}
            </ol>

             
            {/* <p>{cautions}</p> */}
           
            {/* <p>{calories}</p> */}
           
         </div>
    ); 
}

export default Recipe;


