import React, { useEffect, useState } from 'react';
import "./App.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Form } from 'react-bootstrap'
import { Descriptions } from './Components/descriptions'
import Recipe from './Components/Recipes'
// import Form from './Components/Form'


const App = () => {
  const APP_ID = "86978488";
  const APP_KEY = "c5047c9778c7a18228847d20c6a592dd	";

  const [recipes, setRecipes] = useState([]);
  const [search, setSearch] = useState('');
  const [query, setQuery] = useState('chicken');


  const getRecipes = async () => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`);
    const data = await response.json();
    setRecipes(data.hits);
    console.log(data.hits);
  }

  useEffect(() => {
    getRecipes();

  }, [query]);



  const updateSearch = e => {
    setSearch(e.target.value);

  }

  const getSearch = e => {
    e.preventDefault();

    setQuery(search);
    setSearch('');


  }

  return (
    <div className="App">
      <Form className="search-form" onSubmit={getSearch}>
        <input type="text" className="search-bar" placeholder="Enter the Foods you wants to search" value={search} onChange={updateSearch}></input>{' '}
        <button variant="primary" className="search-button">Search</button>
      </Form>
      <div className="recipe">
        {recipes.map(recipe => (
          <Recipe
            key={recipe.recipe.label}
            title={recipe.recipe.label}
            calories={recipe.recipe.calories}
            image={recipe.recipe.image}
            cautions={recipe.recipe.cautions}
            ingredients={recipe.recipe.ingredients}
          />
        ))}
       
      </div>
    </div>
  );

}

export default App;



